/* 🌙 ItemDetails V05 - Design Profissional Escuro */

/* Container Principal - Tema Escuro */
.item-details {
  background: linear-gradient(145deg, #1e293b 0%, #334155 50%, #475569 100%);
  border: 1px solid rgba(148, 163, 184, 0.3);
  border-radius: 16px;
  padding: 1.5rem;
  box-shadow: 
    0 4px 6px -1px rgba(0, 0, 0, 0.3),
    0 2px 4px -1px rgba(0, 0, 0, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.05);
  height: 100%;
  overflow-y: auto;
  backdrop-filter: blur(20px);
  font-size: 0.75rem;
  font-family: 'Inter', 'Segoe UI', system-ui, sans-serif;
}

/* Header Profissional - Escuro */
.item-details-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid rgba(59, 130, 246, 0.2);
  padding-bottom: 1rem;
  margin-bottom: 1.5rem;
  position: relative;
}

.item-details-header::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 60px;
  height: 2px;
  background: linear-gradient(90deg, #3b82f6, #06b6d4);
  border-radius: 1px;
}

.item-details-header h3 {
  margin: 0;
  color: #f1f5f9;
  font-size: 1.25rem;
  font-weight: 700;
  background: linear-gradient(135deg, #f1f5f9, #e2e8f0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Botão Save Profissional */
.save-btn {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  cursor: pointer;
  font-weight: 600;
  font-size: 0.875rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 
    0 4px 14px 0 rgba(16, 185, 129, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
  position: relative;
  overflow: hidden;
}

.save-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.save-btn:hover::before {
  left: 100%;
}

.save-btn:hover {
  transform: translateY(-2px);
  box-shadow: 
    0 8px 25px 0 rgba(16, 185, 129, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.save-btn:active {
  transform: translateY(0);
}

/* Content Layout */
.item-details-content {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

/* Seções de Parâmetros - Escuro */
.parameter-section {
  background: rgba(15, 23, 42, 0.8);
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 16px;
  padding: 1.5rem;
  position: relative;
  backdrop-filter: blur(10px);
  box-shadow: 
    0 1px 3px 0 rgba(0, 0, 0, 0.3),
    0 1px 2px 0 rgba(0, 0, 0, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.05);
}

.parameter-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(59, 130, 246, 0.5), transparent);
}

/* Títulos das Seções - Escuro */
.parameter-section h4 {
  margin: 0 0 1.25rem 0;
  color: #f1f5f9;
  font-size: 1rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  position: relative;
  padding-bottom: 0.75rem;
  border-bottom: 2px solid rgba(59, 130, 246, 0.2);
}

.parameter-section h4::before {
  content: '';
  width: 4px;
  height: 20px;
  background: linear-gradient(135deg, #3b82f6, #06b6d4);
  border-radius: 2px;
  box-shadow: 0 2px 4px rgba(59, 130, 246, 0.3);
}

.parameter-section h4::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 40px;
  height: 2px;
  background: linear-gradient(90deg, #3b82f6, #06b6d4);
  border-radius: 1px;
}

/* Grupos de Parâmetros */
.parameter-group {
  margin-bottom: 1rem;
}

.parameter-group label {
  display: block;
  color: #e2e8f0;
  font-weight: 600;
  margin-bottom: 0.5rem;
  font-size: 0.875rem;
  letter-spacing: 0.025em;
}

/* Inputs Profissionais - Escuro */
.parameter-group input[type="text"],
.parameter-group input[type="number"],
.parameter-group select {
  width: 100%;
  background: rgba(15, 23, 42, 0.9);
  border: 2px solid rgba(148, 163, 184, 0.3);
  border-radius: 12px;
  padding: 0.75rem 1rem;
  color: #f1f5f9;
  font-size: 0.875rem;
  font-weight: 500;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-sizing: border-box;
  backdrop-filter: blur(5px);
  box-shadow: 
    0 1px 2px 0 rgba(0, 0, 0, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.05);
}

.parameter-group input:focus,
.parameter-group select:focus {
  border-color: #3b82f6;
  box-shadow: 
    0 0 0 4px rgba(59, 130, 246, 0.2),
    0 1px 2px 0 rgba(0, 0, 0, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.05);
  outline: none;
  background: rgba(15, 23, 42, 1);
}

.parameter-group input:hover,
.parameter-group select:hover {
  border-color: rgba(59, 130, 246, 0.4);
  background: rgba(15, 23, 42, 1);
}

/* Layout em Linha - parameter-row */
.parameter-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  margin-bottom: 1rem;
  align-items: end;
}

.parameter-row .parameter-group {
  margin-bottom: 0;
}

/* Economy Values Subblock */
.economy-values-subblock {
  background: rgba(15, 23, 42, 0.6);
  border: 1px solid rgba(59, 130, 246, 0.3);
  border-radius: 12px;
  padding: 1.25rem;
  margin-top: 1rem;
  position: relative;
}

.economy-values-subblock h5 {
  margin: 0 0 1rem 0;
  color: #60a5fa;
  font-size: 0.9rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  border-bottom: 1px solid rgba(59, 130, 246, 0.2);
  padding-bottom: 0.5rem;
}

/* 4-column grid for Nominal-Min-QuantMax-QuantMin */
.economy-row-4col {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 0.75rem;
  margin-bottom: 1rem;
}

/* Lifetime + Restock full width row */
.lifetime-restock-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
  margin-bottom: 0;
}

.lifetime-group,
.restock-group {
  position: relative;
}

.lifetime-group label,
.restock-group label {
  font-size: 0.8rem;
  color: #94a3b8;
  margin-bottom: 0.75rem;
}

/* Sliders styling for economy values */
.lifetime-slider,
.restock-slider,
.quantity-slider {
  width: 100%;
  height: 6px;
  border-radius: 3px;
  background: rgba(15, 23, 42, 0.8);
  outline: none;
  -webkit-appearance: none;
  appearance: none;
}

.lifetime-slider::-webkit-slider-thumb,
.restock-slider::-webkit-slider-thumb,
.quantity-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: linear-gradient(135deg, #3b82f6, #1d4ed8);
  cursor: pointer;
  border: 2px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 2px 6px rgba(59, 130, 246, 0.4);
}

.lifetime-slider::-webkit-slider-thumb:hover,
.restock-slider::-webkit-slider-thumb:hover,
.quantity-slider::-webkit-slider-thumb:hover {
  background: linear-gradient(135deg, #1d4ed8, #1e3a8a);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.6);
}

/* Specific slider colors */
.quantmax::-webkit-slider-thumb {
  background: linear-gradient(135deg, #10b981, #059669);
}

.quantmin::-webkit-slider-thumb {
  background: linear-gradient(135deg, #f59e0b, #d97706);
}

/* Flags Grid - Ligáveis com mais espaçamento */
.flags-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 0.75rem;
  margin-top: 0.5rem;
}

.flag-btn {
  background: rgba(15, 23, 42, 0.9);
  border: 2px solid rgba(148, 163, 184, 0.3);
  color: #94a3b8;
  padding: 0.75rem 1rem;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-weight: 600;
  font-size: 0.875rem;
  text-align: center;
  backdrop-filter: blur(5px);
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
  min-height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.flag-btn:hover {
  border-color: rgba(59, 130, 246, 0.4);
  background: rgba(15, 23, 42, 1);
  transform: translateY(-1px);
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.3);
  color: #e2e8f0;
}

.flag-btn.active {
  background: linear-gradient(135deg, #3b82f6, #1d4ed8);
  border-color: #3b82f6;
  color: white;
  box-shadow: 
    0 4px 12px 0 rgba(59, 130, 246, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.flag-btn.active:hover {
  background: linear-gradient(135deg, #1d4ed8, #1e3a8a);
  transform: translateY(-1px);
  box-shadow: 
    0 6px 16px 0 rgba(59, 130, 246, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

/* Usage Buttons */
.usage-buttons {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
  margin-top: 0.5rem;
  margin-bottom: 1rem;
}

.toggle-btn {
  background: rgba(15, 23, 42, 0.9);
  border: 2px solid rgba(148, 163, 184, 0.3);
  color: #94a3b8;
  padding: 0.5rem 0.75rem;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-weight: 600;
  font-size: 0.75rem;
  backdrop-filter: blur(5px);
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
}

.toggle-btn:hover {
  border-color: rgba(16, 185, 129, 0.4);
  background: rgba(15, 23, 42, 1);
  transform: translateY(-1px);
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.3);
  color: #e2e8f0;
}

.toggle-btn.active {
  background: linear-gradient(135deg, #10b981, #059669);
  border-color: #10b981;
  color: white;
  box-shadow: 
    0 4px 12px 0 rgba(16, 185, 129, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.toggle-btn.active:hover {
  background: linear-gradient(135deg, #059669, #047857);
  transform: translateY(-1px);
  box-shadow: 
    0 6px 16px 0 rgba(16, 185, 129, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

/* Tags Buttons */
.tags-buttons {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
  margin-top: 0.5rem;
}

.tag-btn {
  background: rgba(15, 23, 42, 0.9);
  border: 2px solid rgba(148, 163, 184, 0.3);
  color: #94a3b8;
  padding: 0.5rem 0.75rem;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-weight: 600;
  font-size: 0.75rem;
  backdrop-filter: blur(5px);
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
}

.tag-btn:hover {
  border-color: rgba(245, 101, 101, 0.4);
  background: rgba(15, 23, 42, 1);
  transform: translateY(-1px);
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.3);
  color: #e2e8f0;
}

.tag-btn.active {
  background: linear-gradient(135deg, #f56565, #e53e3e);
  border-color: #f56565;
  color: white;
  box-shadow: 
    0 4px 12px 0 rgba(245, 101, 101, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.tag-btn.active:hover {
  background: linear-gradient(135deg, #e53e3e, #c53030);
  transform: translateY(-1px);
  box-shadow: 
    0 6px 16px 0 rgba(245, 101, 101, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

/* Checkboxes Profissionais - Escuro */
.flags-checkbox,
.tier-checkbox {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.flags-checkbox input[type="checkbox"],
.tier-checkbox input[type="checkbox"] {
  width: 18px;
  height: 18px;
  border: 2px solid rgba(148, 163, 184, 0.3);
  border-radius: 6px;
  background: rgba(255, 255, 255, 0.9);
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
}

.flags-checkbox input[type="checkbox"]:checked,
.tier-checkbox input[type="checkbox"]:checked {
  background: linear-gradient(135deg, #3b82f6, #1d4ed8);
  border-color: #3b82f6;
}

.flags-checkbox input[type="checkbox"]:checked::after,
.tier-checkbox input[type="checkbox"]:checked::after {
  content: '✓';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 12px;
  font-weight: bold;
}

.flags-checkbox label,
.tier-checkbox label {
  margin: 0;
  font-size: 0.875rem;
  font-weight: 500;
  color: #e2e8f0;
  cursor: pointer;
}

/* Tier Buttons Profissionais - Escuro */
.tier-buttons {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
  margin-top: 0.5rem;
}

.tier-btn {
  background: rgba(15, 23, 42, 0.9);
  border: 2px solid rgba(148, 163, 184, 0.3);
  color: #94a3b8;
  padding: 0.5rem 1rem;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-weight: 600;
  font-size: 0.875rem;
  min-width: 70px;
  text-align: center;
  backdrop-filter: blur(5px);
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
}

.tier-btn:hover {
  border-color: rgba(59, 130, 246, 0.4);
  background: rgba(15, 23, 42, 1);
  transform: translateY(-1px);
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.3);
  color: #e2e8f0;
}

.tier-btn.active {
  background: linear-gradient(135deg, #3b82f6, #1d4ed8);
  border-color: #3b82f6;
  color: white;
  box-shadow: 
    0 4px 12px 0 rgba(59, 130, 246, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.tier-btn.active:hover {
  background: linear-gradient(135deg, #1d4ed8, #1e3a8a);
  transform: translateY(-1px);
  box-shadow: 
    0 6px 16px 0 rgba(59, 130, 246, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

/* Category Buttons - Escuro */
.category-buttons {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
  margin-top: 0.5rem;
}

.category-btn {
  background: rgba(15, 23, 42, 0.9);
  border: 2px solid rgba(148, 163, 184, 0.3);
  color: #94a3b8;
  padding: 0.5rem 1rem;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-weight: 600;
  font-size: 0.875rem;
  backdrop-filter: blur(5px);
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
}

.category-btn:hover {
  border-color: rgba(16, 185, 129, 0.4);
  background: rgba(15, 23, 42, 1);
  transform: translateY(-1px);
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.3);
  color: #e2e8f0;
}

.category-btn.active {
  background: linear-gradient(135deg, #10b981, #059669);
  border-color: #10b981;
  color: white;
  box-shadow: 
    0 4px 12px 0 rgba(16, 185, 129, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.category-btn.active:hover {
  background: linear-gradient(135deg, #059669, #047857);
  transform: translateY(-1px);
  box-shadow: 
    0 6px 16px 0 rgba(16, 185, 129, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

/* Responsividade */
@media (max-width: 768px) {
  .parameter-row {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }
  
  .economy-row-4col {
    grid-template-columns: 1fr 1fr;
    gap: 0.5rem;
  }
  
  .lifetime-restock-row {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .item-details {
    padding: 1rem;
  }
  
  .parameter-section {
    padding: 1rem;
  }
  
  .economy-values-subblock {
    padding: 1rem;
  }
  
  .tier-buttons,
  .category-buttons {
    gap: 0.25rem;
  }
  
  .tier-btn,
  .category-btn {
    padding: 0.375rem 0.75rem;
    font-size: 0.75rem;
    min-width: 60px;
  }
}

@media (max-width: 480px) {
  .economy-row-4col {
    grid-template-columns: 1fr;
    gap: 0.5rem;
  }
}

/* Estados de Loading - Escuro */
.parameter-group input:disabled,
.parameter-group select:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  background: rgba(15, 23, 42, 0.5);
}

/* Animações suaves */
* {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Scrollbar personalizada - Escuro */
.item-details::-webkit-scrollbar {
  width: 6px;
}

.item-details::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 3px;
}

.item-details::-webkit-scrollbar-thumb {
  background: rgba(59, 130, 246, 0.5);
  border-radius: 3px;
}

.item-details::-webkit-scrollbar-thumb:hover {
  background: rgba(59, 130, 246, 0.7);
}